import {
  PolylineEdgeView,
  RenderingContext,
  SEdge,
  Point,
  toDegrees,
  angle
} from 'sprotty/lib';

import { VNode } from 'snabbdom/vnode';
import * as snabbdom from 'snabbdom-jsx';

const JSX = {createElement: snabbdom.svg};

/**
 * The view used to display Sirius edges.
 *
 * @sbegaudeau
 */
export class SiriusEdgeView extends PolylineEdgeView {
  /**
   * Renders the line of the edge with a specific style configured.
   * @param edge The edge
   * @param segments The segments
   * @param context The context
   */
  protected renderLine(edge: SEdge, segments: Point[], context: RenderingContext): VNode {
    const style: string = (edge as any ).style;

    const firstPoint = segments[0];
    let path = `M ${firstPoint.x},${firstPoint.y}`;
    for (let i = 1; i < segments.length; i++) {
      const p = segments[i];
      path += ` L ${p.x},${p.y}`;
    }
    return <path class-sprotty-edge={true} d={path} style={JSON.parse(style)}/>;
  }

  /**
   * Renders additionals element for the given Edge. This method is overriden in order to let
   * us add decorators to the beggining or the end of the edge (to create an arrow for example).
   * @param edge The edge
   * @param segments The segments
   * @param context The context
   */
  protected renderAdditionals(edge: SEdge, segments: Point[], context: RenderingContext): VNode[] {
    const style: string = (edge as any).style;
    const styleObject = JSON.parse(style);
    styleObject.fill = styleObject.stroke;

    const p1 = segments[segments.length - 2];
    const p2 = segments[segments.length - 1];
    return [
      <path class-edge={true}
            class-arrow={true} d="M 0,0 L 6,-3 L 6,3 Z"
            transform={`rotate(${toDegrees(angle(p2, p1))} ${p2.x} ${p2.y}) translate(${p2.x} ${p2.y})`}
            style={styleObject} />
    ];
  }
}


